.c {
  height: 100vh;
  position: relative;
}
.c-bg {
  width: 20px;
  height: 100%;
  background-color: #f56942;
  position: absolute;
}
.c-wrapper {
  padding: 50px;
  display: flex;
}
.c-left {
  flex: 1;
}
.c-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.c-title {
  font-size: 60px;
  width: 80%;
}
.c-info-item {
  display: flex;

  margin: 50px 0px;
  font-weight: 300;

  /* width: 70%; */
}
.c-icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}
.c-desc {
  font-weight: 200;
}
form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
input {
  width: 100%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 100px;
  font-size: 14px;
  padding-left: 10px;
  outline-color: #f56942;
}
textarea {
  width: 100%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}
button {
  border: none;
  padding: 15px;
  background-color: #f56942;
  color: white;
  font-weight: 500;
  cursor: pointer;
  border-radius: 5px;
}
.errortext {
  color: red;
  width: 100%;
}

@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }
  .c-title {
    font-size: 30px;
  }
  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }
  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }
  input {
    width: 35%;
    height: 40px;
  }
}
