.pl {
  padding: 50px 100px;
  /* background-color: aqua; */
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.pl-texts {
  width: 65%;
}
.pl-title {
  font-size: 50px;
  font-weight: 600;
}
.pl-desc {
  /* background-color: aqua; */
  margin: 20px;
}
.pl-list {
  /* background-color: aliceblue; */
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media screen and (max-width: 480px) {
  /* .pl {
    background-color: red;
  } */
  .pl-texts {
    width: 100%;
  }
  .pl-title {
    font-size: 30px;
  }
}
